import Login from './pages/login.vue'

import Home from './pages/home.vue'

import Edit from './pages/edit.vue'
import Result from './pages/result.vue'
import Detail from './pages/detail.vue'
import Cate from './pages/Cate.vue'
/**
 * meta
 * hideLeft: true
 * module: 
 * menu: 'menu''user'
 */

const routes = [
    {
        path: '/',
        component: Login,
        title: '',
        name: 'Login'
    },
    {
        path: 'home',
        component: Home,
        title: '',
        children: [
            {
                path: 'edit',
                component: Edit,
                name: 'Edit',
                title: '',
                meta: {
                    hideLeft: false,
                }
            },
            {
                path: 'result',
                component: Result,
                name: 'Result',
                title: '',
                meta: {
                    hideLeft: false,
                }
            },
            {
                path: 'detail',
                component: Detail,
                name: 'Detail',
                title: '',
                meta: {
                    hideLeft: false,
                }
            },
            {
                path: 'cate',
                component: Cate,
                name: 'Cate',
                title: '',
                meta: {
                    hideLeft: false,
                }
            }
        ]
    },

]
export default routes



